import Vector2 from "./math/Vector2";

export default class Grid extends Phaser.State {

    private xSize : number;
    private ySize : number;
    private graphics : any;

    constructor(xSize : number, ySize : number) {
        super();
        this.xSize = xSize;
        this.ySize = ySize;
    }
    update() {
        if(this.graphics == null){
            this.graphics = this.game.add.graphics(0, 0);
        }
         this.graphics.clear();
        
        var size :Vector2 = new Vector2(this.game.world.width/this.xSize, this.game.world.height/this.ySize);
        for(var x = 0; x < this.xSize; x++){
            for(var y = 0; y < this.ySize; y++){
                var pos :Vector2 = new Vector2(x * size.x, y * size.y);
                this.graphics.beginFill(Math.random()*0xFFFFFF, 1);
                this.graphics.drawRect(pos.x, pos.y, size.x, size.y);
                this.graphics.endFill();
            }
        }

         (<any>window).graphics = this.graphics;

    }
    create() {
    }
    render() {
        this.game.time.advancedTiming = true;
        var test :string = this.game.time.fps.toString();
        this.game.debug.text('FPS ' + test, 50, 50, 'red');        
    }

}



